import { ContextApi } from 'contexts/Localization/types'
import { PageMeta } from './types'

export const DEFAULT_META: PageMeta = {
  title: 'HighLord',
  description:
    'The most popular AMM on BSC by user count! Earn HIGHLORD through yield farming or win it in the Lottery, then stake it in Syrup Pools to earn more tokens! Initial Farm Offerings (new token launch model pioneered by HighLord), NFTs, and more, on a platform you can trust.',
  image: 'https://highlord.exchange/images/hero.png',
}

export const getCustomMeta = (path: string, t: ContextApi['t']): PageMeta => {
  let basePath
  if (path.startsWith('/swap')) {
    basePath = '/swap'
  } else if (path.startsWith('/add')) {
    basePath = '/add'
  } else if (path.startsWith('/remove')) {
    basePath = '/remove'
  } else if (path.startsWith('/teams')) {
    basePath = '/teams'
  } else if (path.startsWith('/voting/proposal') && path !== '/voting/proposal/create') {
    basePath = '/voting/proposal'
  } else if (path.startsWith('/nfts/collections')) {
    basePath = '/nfts/collections'
  } else if (path.startsWith('/nfts/profile')) {
    basePath = '/nfts/profile'
  } else if (path.startsWith('/pancake-squad')) {
    basePath = '/pancake-squad'
  } else {
    basePath = path
  }

  switch (basePath) {
    case '/':
      return {
        title: `${t('Home')} | ${t('HighLord')}`,
      }
    case '/swap':
      return {
        title: `${t('Exchange')} | ${t('HighLord')}`,
      }
    case '/add':
      return {
        title: `${t('Add Liquidity')} | ${t('HighLord')}`,
      }
    case '/remove':
      return {
        title: `${t('Remove Liquidity')} | ${t('HighLord')}`,
      }
    case '/liquidity':
      return {
        title: `${t('Liquidity')} | ${t('HighLord')}`,
      }
    case '/find':
      return {
        title: `${t('Import Pool')} | ${t('HighLord')}`,
      }
    case '/competition':
      return {
        title: `${t('Trading Battle')} | ${t('HighLord')}`,
      }
    case '/prediction':
      return {
        title: `${t('Prediction')} | ${t('HighLord')}`,
      }
    case '/prediction/leaderboard':
      return {
        title: `${t('Leaderboard')} | ${t('HighLord')}`,
      }
    case '/farms':
      return {
        title: `${t('Farms')} | ${t('HighLord')}`,
      }
    case '/farms/auction':
      return {
        title: `${t('Farm Auctions')} | ${t('HighLord')}`,
      }
    case '/pools':
      return {
        title: `${t('Pools')} | ${t('HighLord')}`,
      }
    case '/lottery':
      return {
        title: `${t('Lottery')} | ${t('HighLord')}`,
      }
    case '/ifo':
      return {
        title: `${t('Initial Farm Offering')} | ${t('HighLord')}`,
      }
    case '/teams':
      return {
        title: `${t('Leaderboard')} | ${t('HighLord')}`,
      }
    case '/voting':
      return {
        title: `${t('Voting')} | ${t('HighLord')}`,
      }
    case '/voting/proposal':
      return {
        title: `${t('Proposals')} | ${t('HighLord')}`,
      }
    case '/voting/proposal/create':
      return {
        title: `${t('Make a Proposal')} | ${t('HighLord')}`,
      }
    case '/info':
      return {
        title: `${t('Overview')} | ${t('HighLord Info & Analytics')}`,
        description: 'View statistics for HighLord exchanges.',
      }
    case '/info/pools':
      return {
        title: `${t('Pools')} | ${t('HighLord Info & Analytics')}`,
        description: 'View statistics for HighLord exchanges.',
      }
    case '/info/tokens':
      return {
        title: `${t('Tokens')} | ${t('HighLord Info & Analytics')}`,
        description: 'View statistics for HighLord exchanges.',
      }
    case '/nfts':
      return {
        title: `${t('Overview')} | ${t('HighLord')}`,
      }
    case '/nfts/collections':
      return {
        title: `${t('Collections')} | ${t('HighLord')}`,
      }
    case '/nfts/profile':
      return {
        title: `${t('Your Profile')} | ${t('HighLord')}`,
      }
    case '/pancake-squad':
      return {
        title: `${t('Pancake Squad')} | ${t('HighLord')}`,
      }
    default:
      return null
  }
}
