import { useEffect } from 'react'
import { useAppDispatch } from 'state'
import { useSelector } from 'react-redux'
import useRefresh from 'hooks/useRefresh'
import { State, LaunchState } from '../types'
import { fetchLaunchDataAsync } from '.'

export const useFetchLaunch = (account) => {
  const { fastRefresh, slowRefresh } = useRefresh()
  const dispatch = useAppDispatch()
  useEffect(() => {
    dispatch(fetchLaunchDataAsync(account))
  }, [dispatch, slowRefresh, account])
}

export const useLaunch = (): { launch: LaunchState } => {
  const { start, end, balance, softCapReached, finalized, minContribution, maxContribution, softCap, hardCap, contribution, tokensPerUSD, participant } = useSelector((state: State) => ({
    start: state.launch.start,
    end: state.launch.end,
    balance: state.launch.balance,
    softCapReached: state.launch.softCapReached,
    finalized: state.launch.finalized,
    minContribution: state.launch.minContribution,
    maxContribution: state.launch.maxContribution,
    softCap: state.launch.softCap,
    hardCap: state.launch.hardCap,
    contribution: state.launch.contribution,
    tokensPerUSD: state.launch.tokensPerUSD,
    participant: state.launch.participant,
  }))
  const launch = {
    start,
    end,
    balance,
    softCapReached,
    finalized,
    minContribution,
    maxContribution,
    softCap,
    hardCap,
    contribution,
    tokensPerUSD,
    participant,
  }
  return { launch }
}