import BigNumber from 'bignumber.js'
import React, { useCallback, useMemo, useState } from 'react'
import styled from 'styled-components'
import { Flex, Text, Button, Modal, LinkExternal, CalculateIcon, IconButton } from '@pancakeswap/uikit'
import { DEFAULT_GAS_LIMIT, DEFAULT_TOKEN_DECIMAL } from 'config'
import { ModalActions, ModalInput } from 'components/Modal'
import RoiCalculatorModal from 'components/RoiCalculatorModal'
import { useTranslation } from 'contexts/Localization'
import useTokenBalance, { FetchStatus, useGetBnbBalance } from 'hooks/useTokenBalance'
import { getFullDisplayBalance, formatNumber, formatBigNumber } from 'utils/formatBalance'
import { getUSDTAddress } from 'utils/addressHelpers'

import tokens from 'config/constants/tokens'

import useToast from 'hooks/useToast'

interface ContributeModalProps {
  onConfirm: (amount: string) => void
  onDismiss?: () => void
  tokenName?: string
  minContribution?: number
  maxContribution?: number
  currentContribution?: number
}

const ContributeModal: React.FC<ContributeModalProps> = ({
  onConfirm,
  onDismiss,
  tokenName = '',
  minContribution = 0,
  maxContribution = 5000,
  currentContribution = 0,
}) => {
  const [val, setVal] = useState('')
  const { toastSuccess, toastError } = useToast()
  const [pendingTx, setPendingTx] = useState(false)
  const { t } = useTranslation()
  const { balance, fetchStatus } = useTokenBalance(getUSDTAddress())

  const fullBalance = useMemo(() => {
    if (fetchStatus === FetchStatus.SUCCESS) {
      return getFullDisplayBalance(new BigNumber(balance))
    }
    return getFullDisplayBalance(new BigNumber(0))
  }, [balance, fetchStatus])

  const lpTokensToStake = new BigNumber(val)
  const fullBalanceNumber = new BigNumber(fullBalance)

  const handleChange = useCallback(
    (e: React.FormEvent<HTMLInputElement>) => {
      if (e.currentTarget.validity.valid) {
        setVal(e.currentTarget.value.replace(/,/g, '.'))
      }
    },
    [setVal],
  )

  const handleSelectMax = useCallback(() => {
    setVal(fullBalance)
  }, [fullBalance, setVal])

  return (
    <Modal title={t(`Contribute ${tokenName}`)} onDismiss={onDismiss}>
       <ModalInput
        value={val}
        onSelectMax={handleSelectMax}
        onChange={handleChange}
        max={fullBalance}
        symbol={tokenName}
      /> 
      <ModalActions>
        <Button variant="secondary" onClick={onDismiss} width="100%" disabled={pendingTx}>
          {t('Cancel')}
        </Button>
        <Button
          width="100%"
          disabled={
            pendingTx || !lpTokensToStake.isFinite() || lpTokensToStake.eq(0) || lpTokensToStake.gt(fullBalanceNumber)
          }
          onClick={async () => {
            setPendingTx(true)
            const amount = new BigNumber(val)
            try {
              if(amount.gte(new BigNumber(minContribution)) && amount.plus(new BigNumber(currentContribution)).lte(new BigNumber(maxContribution))){
                await onConfirm(val)
                toastSuccess(t('Successed!'), t('Your funds have been contributed in the pre-sale'))
                onDismiss()                
              }else{
                toastError(
                  t('Error'),
                  t('Check the contribution limit value. and Please try again!'),
                )
              }

            } catch (e) {
              toastError(
                t('Error'),
                t('Transaction was failed. Please check pre-sale information again and try again!'),
              )
              console.error(e)
            } finally {
              setPendingTx(false)
            }
          }}
        >
          {pendingTx ? t('Confirming') : t('Confirm')}
        </Button>
      </ModalActions>
    </Modal>
  )
}

export default ContributeModal
