import React, { useEffect, useState } from 'react'
import styled from 'styled-components'
import BigNumber from 'bignumber.js'
import { useWeb3React } from '@web3-react/core'
import { parseUnits } from 'ethers/lib/utils'
import { ethers } from 'ethers'
import { Card, CardBody, Text, Heading, BaseLayout, Button, LinkExternal, Progress, Flex, Image, useModal } from '@pancakeswap/uikit'
import { useTranslation } from 'contexts/Localization'
import { Launch, LaunchStatus } from 'config/constants/types'
import { usePresale, useERC20 } from 'hooks/useContract'
import { useFetchLaunch, useLaunch } from 'state/launch/hooks'
import { getBscScanLink } from 'utils'
import truncateHash from 'utils/truncateHash'
import { getAddress, getUSDTAddress } from 'utils/addressHelpers'
import { ToastDescriptionWithTx } from 'components/Toast'
import useToast from 'hooks/useToast'
import { getFullDisplayBalance, getBalanceAmount } from 'utils/formatBalance'
import ConnectWalletButton from 'components/ConnectWalletButton'
import ContributeModal from './ContributeModal'
import LaunchTime from './LaunchTime'
import useContribute from '../../hooks/useContribute'
import useClaim from '../../hooks/useClaim'
import useRefund from '../../hooks/useRefund'
import useApproveLaunch from '../../hooks/useApproveLaunch'
import { EnableStatus } from './types'

const PresaleCardWrapper = styled(Card)`
  width: 100%;
  min-height: 880px;
  margin: 32px auto;
`

const TokenImage = styled.img`
  margin-right: 20px;
`

const StyledProgress = styled(Progress)`
  margin-top: 20px;
  width: 100%;
`

const ExternalLink = styled(LinkExternal)`
  color: ${({ theme }) => theme.colors.text};
  font-weight: normal;
  font-size: 20px;

  svg {
    fill: ${({ theme }) => theme.colors.text};
  }
`

const StyledBox = styled(Flex)`
  flex-direction: column;
  width: 100%;
  border-radius: 10px;
  border: 1px solid ${({ theme }) => theme.colors.text};
  padding: 15px;
  margin-top: 20px;
`

export interface PresaleCardProps {
  launch: Launch
}

const getValueAsEthersBn = (amount: string) => {
  const valueAsFloat = parseFloat(amount)
  return Number.isNaN(valueAsFloat) ? ethers.BigNumber.from(0) : parseUnits(amount)
}

const getStatus = (currentTime: number, startTime: number, endTime: number): LaunchStatus | null => {
  if (currentTime < startTime) {
    return 'coming_soon'
  }

  if (currentTime >= startTime && currentTime <= endTime) {
    return 'live'
  }

  if (currentTime > endTime) {
    return 'finished'
  }

  return null
}

const PresaleCard: React.FC<PresaleCardProps> = ({launch}) => {
  const { t } = useTranslation()
  const { account, library } = useWeb3React()
  const {id, address, isActive, symbol, name, currency, presaleAddress, tokenDecimals, tokenAllocation, price} = launch
  const presaleContract = usePresale()
  const { onContribute } = useContribute()
  const { onClaim } = useClaim()
  const { onRefund } = useRefund()
  const { launch: launchData } = useLaunch()
  const [loading, setLoading] = useState(true)
  const [status, setStatus] = useState(null)
  const [progress, setProgress] = useState(0)
  const [minContribution, setMinContribution] = useState(0)
  const [maxContribution, setMaxContribution] = useState(0)
  const [softCap, setSoftCap] = useState(0)
  const [hardCap, setHardCap] = useState(0)
  const [softPercent, setSoftPercent] = useState(0)
  const [hardPercent, setHardPercent] = useState(0)
  const [softCapReached, setSoftCapReached] = useState(false)
  const [finalized, setFinalized] = useState(false)
  const [secondsStart, setSecondsStart] = useState(0)
  const [secondsEnd, setSecondsEnd] = useState(0)
  const [contribution, setContribution] = useState(0)
  const [tokensPerUSD, setTokensPerUSD] = useState(0)


  const [participant, setParticipant] = useState(null)
  const [enableStatus, setEnableStatus] = useState(EnableStatus.DISABLED)

  useFetchLaunch(account)

  const usdtTokenContract = useERC20(getUSDTAddress())
  const onApprove = useApproveLaunch(usdtTokenContract, presaleContract.address)
  const { toastSuccess } = useToast()

  const handleApprove = async () => {
    try {
      setEnableStatus(EnableStatus.IS_ENABLING)

      const receipt = await onApprove()

      setEnableStatus(EnableStatus.ENABLED)
      toastSuccess(
        t('Successfully Enabled!'),
        <ToastDescriptionWithTx txHash={receipt.transactionHash}>
          {t('You can now participate in the %tokenSymbol% ICO.', { tokenSymbol: symbol })}
        </ToastDescriptionWithTx>,
      )
    } catch (error) {
      setEnableStatus(EnableStatus.DISABLED)
    }
  }

  useEffect(() => {
    const checkAllowance = async () => {
      try {
        const response = await usdtTokenContract.allowance(account, presaleContract.address)
        const currentAllowance = new BigNumber(response.toString())
        setEnableStatus(currentAllowance.lte(0) ? EnableStatus.DISABLED : EnableStatus.ENABLED)
      } catch (error) {
        setEnableStatus(EnableStatus.DISABLED)
      }
    }

    if (account) {
      checkAllowance()
    }
  }, [account, usdtTokenContract, presaleContract, setEnableStatus])
  
  useEffect(() => {
    const currentTime = Math.round(Date.now() / 1000)
    setStatus(getStatus(currentTime, launchData.start, launchData.end))
    setProgress(currentTime > launchData.start ? ((currentTime - launchData.start) / (launchData.end - launchData.start)) * 100 : (10000/(launchData.start - currentTime)) * 100)
    setSoftPercent((launchData.contribution && launchData.softCap) ? (launchData.contribution.div(launchData.softCap).times(100)).toNumber() : 0)
    setHardPercent((launchData.contribution && launchData.hardCap) ? (launchData.contribution.div(launchData.hardCap).times(100)).toNumber() : 0)
    setSecondsStart(launchData.start)
    setSecondsEnd(launchData.end)
    setSoftCapReached(launchData.softCapReached)
    setFinalized(launchData.finalized)
    setMinContribution(launchData.minContribution ? getBalanceAmount(launchData.minContribution).toNumber() : 0)
    setMaxContribution(launchData.maxContribution ? getBalanceAmount(launchData.maxContribution).toNumber() : 0)
    setSoftCap(launchData.softCap ? getBalanceAmount(launchData.softCap).toNumber() : 0)
    setHardCap(launchData.hardCap ? getBalanceAmount(launchData.hardCap).toNumber() : 0)
    setContribution(launchData.contribution ? getBalanceAmount(launchData.contribution).toNumber() : 0)
    setTokensPerUSD(launchData.tokensPerUSD ? getBalanceAmount(launchData.tokensPerUSD).toNumber() : 0)
    setParticipant(launchData.participant)
    if(launchData.start)
      setLoading(false)
  }, [launchData, launch])

  const contributedAmount = ( participant && participant.contributionUSD ) ? new BigNumber(getFullDisplayBalance(new BigNumber(participant.contributionUSD.toString()))).toNumber() : 0
  const totalTokenAmount = contributedAmount * tokensPerUSD
  const cliamedTokenAmount = ( participant && participant.claimedToken ) ?  new BigNumber(getFullDisplayBalance(new BigNumber(participant.claimedToken.toString()))).toNumber() : 0
  const isFinished = status === 'finished' || finalized
  const isWaiting = cliamedTokenAmount > 0

  const handleContribute = async (amount: string) => {
    await onContribute(amount)
  }

  const handleClaim = async () => {
    await onClaim()
  }

  const handleRefund = async () => {
    await onRefund()
  }

  const [onContributeModal] = useModal(
    <ContributeModal
      onConfirm={handleContribute}
      tokenName="USDT"
      minContribution={minContribution}
      maxContribution={maxContribution}
      currentContribution={contributedAmount}
    />,
  )

  const renderEnableButton = () => {
    return (
      <Button onClick={handleApprove} className='agamaPrimaryBtn disabled-btn agamaBtnSm' mt="10px" width="200px" variant="secondary" >
        {t('Enable')}
      </Button>
    )
  }

  const renderActionButton = () => {
    return isFinished ? (
      (
        softCapReached ?
          <Button onClick={handleClaim} mt="10px" width="200px" variant="secondary" >
            {t('Claim')}
          </Button>
          :
          <Button onClick={handleRefund} mt="10px" width="200px" variant="secondary" >
            {t('Refund')}
          </Button>
      )
    ) : (
      hardPercent < 100 ?
      <Button onClick={onContributeModal} mt="10px" width="200px" variant="secondary" >
        {t('Contribute')}
      </Button>
      :
      <Flex alignItems="center" mb="20px">
        <Text fontSize="16px">{t('Wait until enabled claim')}</Text>
      </Flex>
    )
  }

  return (
    <PresaleCardWrapper>
      <CardBody>
        <Flex alignItems="center" mb="20px">
          <TokenImage src="/images/launch/logo.png" alt={id} width="80px" height="80px" />
          <Heading scale="xl">{t(`${name}`)}</Heading>
        </Flex>
        <Flex flexDirection="column">
          <StyledProgress primaryStep={progress}/>
          <Flex justifyContent="center" mt="5px">
            <LaunchTime isLoading={loading} isFinish={isFinished}  status={status} secondsUntilStart={secondsStart} secondsUntilEnd={secondsEnd} />
          </Flex>
        </Flex>
        <Flex alignItems="center" justifyContent="space-between" mt="30px">
          <Text fontSize="20px">{t('Name')}</Text>
          <Text fontSize="20px">{name}</Text>
        </Flex>
        <Flex alignItems="center" justifyContent="space-between">
          <Text fontSize="20px">{t('Symbol')}</Text>
          <Text fontSize="20px">{symbol}</Text>
        </Flex>
        <Flex alignItems="center" justifyContent="space-between">
          <Text fontSize="20px">{t('Decimals')}</Text>
          <Text fontSize="20px">{tokenDecimals}</Text>
        </Flex>
        <Flex alignItems="center" justifyContent="space-between">
          <Text fontSize="20px">{t('Token Address')}</Text>
          <ExternalLink href={getBscScanLink(getAddress(address), 'address')}>{truncateHash(getAddress(address))}</ExternalLink>
        </Flex>
        <Flex alignItems="center" justifyContent="space-between">
          <Text fontSize="20px">{t('Presale Price')}</Text>
          <Text fontSize="20px">{t(`1 USDT = ${tokensPerUSD} `) + symbol}</Text>
        </Flex>
        <StyledBox>
          <Flex alignItems="center" justifyContent="space-between">
            <Text fontSize="20px">{t('Min Contribution')}</Text>
            <Text fontSize="20px">{t(`${minContribution} USDT`)}</Text>
          </Flex>
          <Flex alignItems="center" justifyContent="space-between">
            <Text fontSize="20px">{t('Max Contribution')}</Text>
            <Text fontSize="20px">{t(`${maxContribution} USDT`)}</Text>
          </Flex>
          <Flex alignItems="center" justifyContent="space-between">
            <Text fontSize="20px">{t('Soft Cap')}</Text>
            <Text fontSize="20px">{t(`${softCap} USDT`)}</Text>
          </Flex>
          <Flex alignItems="center" justifyContent="space-between" mb="10px">
            <Text fontSize="20px">{t('Hard Cap')}</Text>
            <Text fontSize="20px">{t(`${hardCap} USDT`)}</Text>
          </Flex>
          <StyledProgress primaryStep={hardPercent} secondaryStep={softPercent}/>
          <Flex alignItems="center" justifyContent="space-between" mt="10px">
            <Text fontSize="20px">{t('Your Contribution')}</Text>
            <Text fontSize="20px">{t(`${contribution} USDT`)}</Text>
          </Flex>
          {(loading || isWaiting || status === 'coming_soon' || (!isFinished && status === 'live' && hardPercent >= 100)) ?
              <Flex alignItems="center" justifyContent="center" mt="10px">
                <Button disabled mt="10px" width="200px" variant="secondary" >
                      {t('Waiting for ...')}
                </Button>
              </Flex>            
              :
              <Flex alignItems="center" justifyContent="center" mt="10px">
                {enableStatus !== EnableStatus.ENABLED && account ? (
                  renderEnableButton()
                ):(!account ? <ConnectWalletButton mt="10px" width="200px" variant="secondary" /> : renderActionButton())}
              </Flex>
          }
        </StyledBox>
      </CardBody>
    </PresaleCardWrapper>
  )
}

export default PresaleCard