import { createSlice } from '@reduxjs/toolkit'
import BigNumber from 'bignumber.js'
import { LaunchState, AppThunk } from 'state/types'
import { fetchBalance, fetchStartTime, fetchEndTime, fetchSoftCapReached, fetchFinalized, fetchMinContribution, fetchMaxContribution, fetchSoftCap, fetchHardCap, fetchContribution, fetchTokensPerUSD, fetchParticipants } from './fetchLaunch'

const initialState: LaunchState = {
  start: null,
  end: null,
  balance: null,
  softCapReached: false,
  finalized: false,
  minContribution: new BigNumber(0),
  maxContribution: new BigNumber(0),
  softCap: new BigNumber(0),
  hardCap: new BigNumber(0),
  contribution: new BigNumber(0),
  tokensPerUSD: new BigNumber(0),
  participant: null
}



export const fetchLaunchDataAsync = (account): AppThunk => async (dispatch) => {
  const launchData = {
    start: await fetchStartTime(),
    end: await fetchEndTime(),
    balance: await fetchBalance(),
    softCapReached: await fetchSoftCapReached(),
    finalized: await fetchFinalized(),
    minContribution: await fetchMinContribution(),
    maxContribution: await fetchMaxContribution(),
    softCap: await fetchSoftCap(),
    hardCap: await fetchHardCap(),
    contribution: await fetchContribution(),
    tokensPerUSD: await fetchTokensPerUSD(),
    participant: account ? await fetchParticipants(account) : null
  }

  dispatch(setLaunchData(launchData))
}

export const LaunchSlice = createSlice({
  name: 'Launch',
  initialState,
  reducers: {
    setLaunchData: (state, action) => {
      const launchData = action.payload
      if (!state.start) {
        state.start = launchData.start
      }
      if (!state.end) {
        state.end = launchData.end
      }      
      state.balance = launchData.balance
      state.softCapReached = launchData.softCapReached
      state.finalized = launchData.finalized
      state.minContribution = launchData.minContribution
      state.maxContribution = launchData.maxContribution
      state.softCap = launchData.softCap
      state.hardCap = launchData.hardCap
      state.contribution = launchData.contribution
      state.tokensPerUSD = launchData.tokensPerUSD
      state.participant = launchData.participant
    },
  },
})

// Actions
export const { setLaunchData } = LaunchSlice.actions

export default LaunchSlice.reducer